import React from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import BackButton from "../../components/common/BackButton";
import ChoosenProductCardTwo from "./ChoosenProductCardTwo";

export default function ChoosenProductListTwo({ addClass }) {
	const { productList } = useSelector(state => state.choosenProducts);
	const { t } = useTranslation();
	return (
		<div className="choosen-product-list">
			<div className="choosen-product-list_header">
				<BackButton />
				<h3>{t("choosen")}</h3>
			</div>
			<div className="container choosen-product-list_body">
				<div className="row p-0 m-0">
					{productList.length > 0 ? (
						productList.map((product, i) => {
							return (
								<ChoosenProductCardTwo
									key={i}
									product={product}
								/>
							);
						})
					) : (
						<h3 className="empty">{t("empty")}</h3>
					)}
				</div>
			</div>
		</div>
	);
}
