import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { formatterCurrency } from "../../utils/string";
import { setChoosenProduct } from "../../actions/choosenProducts";
import { getImageUrl } from "../../utils/oxMethods";

export default function ChoosenProductCardTwo({ product }) {
	const dispatch = useDispatch();
	return (
		<div className="col-md-6 col-sm-12">
			<div className="choosen-product_two">
				<div className="product_img_two">
					<span
						onClick={() => {
							dispatch(setChoosenProduct(product));
						}}
					>
						<img
							src="/assets/images/vibo/choosen-heart.svg"
							alt="favourite icon"
						/>
					</span>
					<Link to={`/product/${product.variation.id}`}>
						<img
							src={getImageUrl(
								product.variation.images[0],
								"150x_"
							)}
							alt={product.variation.name}
						/>
					</Link>
				</div>
				<div className="product_body_two">
					<div className="product-price">
						{0 < product.discount ? (
							<span className="old-price">
								{formatterCurrency(product.oldPrice)}
							</span>
						) : null}
						<span className="new-price">
							{formatterCurrency(product.newPrice)}
						</span>
					</div>
					<div className="product_name">
						<p>{product.variation.name}</p>
					</div>
				</div>
			</div>
		</div>
	);
}
